@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{transition:background 1500ms ease;display:flex;justify-content:center;align-items:center;width:100vw;height:100vh;text-align:center;font-family:Raleway, sans-serif}#quote-box{display:flex;flex-direction:column;background:white;border-radius:4px;padding:40px 50px;max-width:550px}#quote-box #text{transition:color 1500ms ease, opacity 1000ms ease;font-size:1.75em;line-height:1.2em}#quote-box #text i{margin-right:10px}#quote-box #author{transition:color 1500ms ease, opacity 1000ms ease;text-align:end;margin-top:10px}.buttons-container{display:flex;margin-top:10px}.buttons-container .link{color:white;border-radius:3px;transition:background 1500ms ease;display:flex;justify-content:center;align-items:center;min-width:40px;box-sizing:border-box;text-decoration:none;padding:0.5em;margin:0 5px}.buttons-container .link:hover{box-shadow:inset 0 0 0 2em rgba(255,255,255,0.3)}.buttons-container .link .link-icon{color:white;font-size:1.2em}.buttons-container #new-quote{color:white;border-radius:3px;transition:background 1500ms ease;outline:none;border:none;font-size:0.9em;padding:5px 15px}.buttons-container #new-quote:hover{box-shadow:inset 0 0 0 2em rgba(255,255,255,0.3)}.buttons-container .spacer{flex:1 1}

