@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;400;700&display=swap');

@mixin button-style {
  color: white;
  border-radius: 3px;
}

@mixin background-fade {
  transition: background 1500ms ease;
}

@mixin text-fade {
  transition: color 1500ms ease, opacity 1000ms ease;
}

@mixin lighter {
  box-shadow: inset 0 0 0 2em rgba(255, 255, 255, 0.3);
}

.App {
  @include background-fade;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  text-align: center;
  font-family: Raleway, sans-serif;
}


#quote-box {
  display: flex;
  flex-direction: column;
  background: white;
  border-radius: 4px;
  padding: 40px 50px;
  max-width: 550px;

  #text {
    @include text-fade;
    font-size: 1.75em;
    line-height: 1.2em;

    i {
      margin-right: 10px;
    }
  }

  #author {
    @include text-fade;
    text-align: end;
    margin-top: 10px;
  }
}

.buttons-container {
  display: flex;
  margin-top: 10px;

  .link {
    @include button-style;
    @include background-fade;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 40px;
    box-sizing: border-box;
    text-decoration: none;
    padding: 0.5em;
    margin: 0 5px;

    &:hover {
      @include lighter;
    }

    .link-icon {
      color: white;
      font-size: 1.2em;
    }
  }

  #new-quote {
    @include button-style;
    @include background-fade;
    outline: none;
    border: none;
    font-size: 0.9em;
    padding: 5px 15px;

    &:hover {
      @include lighter;
    }
  }

  .spacer {
    flex: 1;
  }
}




